.modal {
    display:none;
    position: fixed; /* Stay in place */
    z-index: 103; /* Sit on top */
    left: 0;
    top: 0;
    width: 100%; /* Full width */
    height: 100%; /* Full height */
    overflow: hidden; /* Enable scroll if needed */
    background-color: rgb(0,0,0); /* Fallback color */
    background-color: rgba(0,0,0,0.4); /* Black w/ opacity */
}

/* Modal Content/Box */

.modal-body {
    background-color: $white;
    margin: 10% auto; /* 15% from the top and centered */
    padding: 40px;
    border: 1px solid #888;
    width: 80%; /* Could be more or less, depending on screen size */
    max-width:800px;
    clear:both;
    *zoom:1;
    position:relative;
}

.modal-body:before { /* 15% from the top and centered */ /* Could be more or less, depending on screen size */
    content:'';
    display:table;
}

.modal-body:after { /* 15% from the top and centered */ /* Could be more or less, depending on screen size */
    content:'';
    display:table;
    clear:both;
}

.modal-body:before, .modal-body:after {
    content:" ";
    clear:both;
    display:block;
}

.modal-close {
    position:absolute;
    right:-12px;
    top:-12px;
    overflow:hidden;
    display:block;
    text-decoration: none;
}

.modal-close:before {
    opacity:0.7;
    background:$black;
    color:$white;
    padding:0px;
    border-radius:50%;
    font-size:32px;
}

.fancybox-inner .modal-content {
    display:block !important;
}

.modal-content{
    display:none !important;
}

